(() => {
  // select all drawer buttons and drawers
  const drawerButtons = document.querySelectorAll(".js-open-drawer");

  drawerButtons.forEach((drawerButton) => {
    const drawerId = drawerButton.getAttribute("aria-controls");
    const drawer = document.querySelector(`#${drawerId}`);

    // open / close the drawer
    drawerButton.addEventListener("click", () => {
      const isExpanded = drawerButton.getAttribute("aria-expanded") === "true";
      drawerButton.setAttribute("aria-expanded", !isExpanded);
      drawer.hidden = isExpanded;
      drawer.classList.toggle("feature-cards__content--open", !isExpanded);
    });
  });

  document.querySelectorAll(".site-header__nav-link").forEach((navLink) => {
    navLink.addEventListener("click", function (e) {
      e.preventDefault();

      // Get the target section ID from the href attribute
      const targetId = this.getAttribute("href");
      const target = document.querySelector(targetId);

      if (target) {
        const headerHeight = 110;
        const targetPosition =
          target.getBoundingClientRect().top + window.scrollY - headerHeight;

        // Scroll to the target position
        window.scrollTo({
          top: targetPosition,
          behavior: "smooth",
        });
      }
    });
  });

  // Select all navigation links and sections
  const navLinks = document.querySelectorAll(".site-header__nav-link");
  const sections = document.querySelectorAll("section");

  // Helper function to remove the active class from all links
  function clearActiveLinks() {
    navLinks.forEach((link) =>
      link.classList.remove("site-header__nav-link--active")
    );
  }

  // Scroll event listener
  window.addEventListener("scroll", () => {
    let currentSectionId = null;

    // Check each section's position relative to the viewport
    sections.forEach((section) => {
      const sectionTop = section.offsetTop - 110; // Adjust for header height
      const sectionBottom = sectionTop + section.offsetHeight;

      if (window.scrollY + 1 >= sectionTop && window.scrollY < sectionBottom) {
        currentSectionId = section.id;
      }
    });

    // Update active class on navigation links
    if (currentSectionId) {
      clearActiveLinks();
      const activeLink = document.querySelector(
        `a[href="#${currentSectionId}"]`
      );
      if (activeLink) {
        activeLink.classList.add("site-header__nav-link--active");
      }
    } else {
      // If no section is visible, clear active classes
      clearActiveLinks();
    }
  });
})();
